import Home from '../components/Home.vue'
import PersonCenter from '../components/Person_center.vue'
import Carts from '../components/Carts.vue'
import CommodityDetails from '../components/Commodity_details.vue'
import Register from '../components/Register.vue'
import Login from '../components/Login.vue'
export default{
    /* mode:'history',
    fallback:true,  */
    routes:[
        { path: '/home', component: Home,name:'home' },
        { path: '/person_center/:userId?/:userName?', component: PersonCenter,name:'person_center',props:true },
        { 
            path: '/carts', 
            component: Carts,
            children:[
                { path: 'commodityId/:commodityId', component: CommodityDetails }
            ]

        },
        { 
            path: '/commodityId/:commodityId', 
            component: CommodityDetails
        },
        { 
            path: '/login', 
            component: Login
        },
        { 
            path: '/register', 
            component: Register
        },
        { path: '*', redirect: '/home' }
    ]
}


